<template lang="">
<div class="financial-content">
  <div class="jqqd" v-if="false">
    {{tipMessage}}
  </div>
  <!-- <mt-cell title="认购" value="更多" :to="{path:'/financialMore',query:{stkBiz:stkBizs.subscribePurchase}}" is-link class="stk-type"></mt-cell> -->
  <div v-if="listRgsg.length==0" class=" tip-message"> 暂无数据</div>
   <mt-loadmore :top-method="refresh" :bottom-method="loadMore" :bottom-all-loaded="loading" ref="loadmore" >
  <ul class="list-infinite list-infinite-no">
    <li v-for="(item,index) in listRgsg">
      <router-link :to="{path:'/financial/productDetail',query:{issNo:item.issNo,stkBiz:stkBizs.subscribePurchase}}">
        <div class="financial-box">
          <div>{{item.stkName}}</div>
          <div class="circle-progress-contain">
            <circleProgress :num="jindu(item)" ></circleProgress>
          </div>

        </div>
        <div class="financial-box little-tile">
          <div>
            价格:<span clsss="big-info" style="font-size: 18px;color: #f00;">{{item.issPrice | toMoney}}</span>元/<span v-remoteoptionsspan="dicts.unit">{{item.unit}}</span>
          </div>
          <div style="height: 20px;line-height: 22px;">
            梯度:{{item.baseQty}}

          </div>
          <div style="height: 20px;line-height: 22px;">
            可购:{{kegou(item)}}

          </div>
        </div>
      </router-link>
    </li>
  </ul>
</mt-loadmore>
  <transition name="slide-right">
    <router-view class="cover-transition"></router-view>
  </transition>
</div>
</template>
<script>
import cookieUtil from "js-cookie";
import {
  Cell
} from 'mint-ui'
import circleProgress from 'components/circleProgress.vue'
import {
  Navbar,
  TabItem,
  Loadmore,
  InfiniteScroll
} from "mint-ui";
import {
  Toast
} from "mint-ui";
Vue.use(InfiniteScroll);
Vue.use(Loadmore.name,Loadmore);
Vue.component(Cell.name, Cell);

export default {
  data() {
    return {
      dicts: {
        unit: {
          name: "UNIT"
        }
      },
      tipMessage: "加载中...",
      cacheList: {},
      loading: true,
      list: [],
      listZjsg: [],
      listRgsg: [
        // {stkName:"安级没戏这是一个项目名称 （第一期）A1标段项目",
        //         unit:"克",
        //         baseQty:2,
        //         issSize:20000,//发行规模
        //         applyAmt:15000.00//当前申购金额

        // }
      ],
          allList:[],
      listPgsb: [],
      listSzps: [],
      listDxps: [],
      serverConfig: {
        order: '', //排序 不填 默认字段
        stkex: global.scdm, //
        expectRateFlag: 0, // 预期年绿化率" 0 全部
        minBuyAmt: 0, //起购金额 0不限
        periodFlag: 0, //剩余期限//0不限
        offset: 0,
        limit: global.pageSize,
        stkBiz: global.STKBIZ.SUBSCRIBE_PURCHASE
      },
      stkBizs: {
        subscribe: global.STKBIZ.SUBSCRIBE, //资金申购  默认这个  不做认购申购
        subscribePurchase: global.STKBIZ.SUBSCRIBE_PURCHASE,
        declareIssue: global.STKBIZ.DECLARE_ISSUE, //配股申报
        marketSell: global.STKBIZ.MARKET_SELL, //市值配售
        privateSell: global.STKBIZ.PRIVATE_SELL //定向配售
      }
    };
  },
  created: function() {
    var self = this;
    var c1 = {
      order: '', //排序 不填 默认字段
      stkex: global.scdm, //
      expectRateFlag: 0, // 预期年绿化率" 0 全部
      minBuyAmt: 0, //起购金额 0不限
      periodFlag: 0, //剩余期限//0不限
      offset: 0,
      limit: global.pageSize,
      stkBiz: this.stkBizs.subscribePurchase
    };
    axiosUtil.upCltIssStkQry(c1).then(function(data) {
      self.listRgsg = data.data.rows;
    })
},
filters: {
    dateFormat: function(time) {
      return dateFormatter(time);
    },

    toMoney: function(value) {
      return toMoney(value);
    }
  },
  components: {
    circleProgress
  },
  methods: {
      getDataList:function(){
        var self = this;
     return  axiosUtil.upCltIssStkQry(this.serverConfig).then(data=> {
        var data=data.data;
                this.listRgsg= data.rows//默认所有
            if(  this.listRgsg.length==0){
             this.tipMessage="暂无数据"
            }
            return data;
      })

      },
      refresh:function(){
        this.serverConfig.offset=0;
        this.getDataList().then(data=>{
         this.allList=data.rows;
         this.$refs.loadmore.onTopLoaded();
         if(data.total==this.allList.length){
        this.loading=true;
       }else{
        this.loading=false;
       }
      });

      },
      loadMore: function() {
          let self = this;
          if(!this.loading){
              this.loading = true;
            this.serverConfig.offset += global.pageSize;
            this.getDataList(data=>{
              self.allList = self.allList.concat(data.rows);
               self.loading = false;
               if (data.total == self.allList.length) {
                 self.loading = true;
                 Toast({
                   message: "已加载最后一条",
                   position: "bottom",
                   duration: 1000
                 });
               }
            })
          }
            
      },
    kegou: function(item) {
      if (!item.applyAmt)
        item.applyAmt = 0;
      return item.issSize.subtr(item.applyAmt)
    },
    jindu: function(item) {
      // return item.applyAmt.div(item.issSize)
      if (!item.applyAmt)
        item.applyAmt = 0;
      return item.applyAmt.div(item.subsribedAllotmentQty)
    },
    financialDetail: function(index) {
      // this.$router.push("/financial/detail?issNo=" + this.list[index].issNo);
    }
  }
};
</script>
<style scoped>
.financial-box {
  display: -webkit-flex;
  display: flex;
}

.financial-box>div {
  flex-grow: 1;
  line-height:21px;
}

.nhsyl {
  font-size: 26px;
  color: #ef4f4f;
}

.nhsyl-text {
  font-size: 14px;
  color: #bbb;
}

.qx {
  margin-top: 12px;
}

.qx-text {
  font-size: 14px;
  color: #7089ff;
  border-right: 1px solid #ddd;
  padding: 0 4px;
  margin-right: 4px;
}

.app-content {
  height: 100%;
  background: #111;
}

.financial-content {
  height: 100%;
  background: #efeee9;
  padding-bottom: 44px;
  overflow-y: auto;
  margin-top: 3px;
}
.big-info{
    font-size:18px;
    color:#f00;
}
::-webkit-scrollbar {
  width: 0px;
  height: 1px;
}

::-webkit-scrollbar-thumb {
  border-radius: 5px;
  -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
  background: rgba(0, 0, 0, 0.2);
}

.list-infinite-no {
  height: auto;
  padding-bottom: 24px;
  width: 100%;
}

.list-infinite-no li {
  margin-top: 0;
  border-bottom: 1px solid #efefef;
}

.list-infinite-no li a {
  text-decoration: none;
  color: #111;
}

.stk-type {
  background: #eee;
}

.tip-message {
  text-align: center;
  margin-top: 24px;
}

.little-tile {
  font-size: 12px;
  color: #aaa;
}

.circle-progress-contain>div {
  float: right;
}

</style>



